.services-content {
    width: 100%;
    height: 300px;
    background: url(../../Images/bg_image_3.jpg);
    background-attachment: fixed;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 0% 70%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    filter: contrast(70%);
  }
  .img-text {
    width: 150px;
    height: 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    //border: 1px purple solid;
    li {
      color: white;
      font-size: 18px;
    }
    .i-link {
      text-decoration: none;
      color: white;
      font-size: 18px;
    }
  }
  
  .page-name {
    width: 120px;
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    //border: 1px green solid;
    h1 {
      color: white;
      font-size: 40px;
      font-weight: 600;
    }
  }